$bg-checkered-fg: $gray-200 !default;
$inline-scroller-max-height: 125px !default;

// Autofit Row

$autofit-col-expand-min-width: 3.125rem !default; // 50px

$autofit-padded-col-padding-x: 0.5rem !default; // 8px
$autofit-padded-col-padding-y: 0.25rem !default; // 4px

$autofit-padded-no-gutters-sm: () !default;
$autofit-padded-no-gutters-sm: map-merge(
	(
		margin: -0.25rem,
		width: auto,
		autofit-col: (
			padding: 0.25rem,
		),
	),
	$autofit-padded-no-gutters-sm
);

// Close

$close-color: $black !default;
$close-font-size: $font-size-base * 1.5 !default;
$close-font-weight: $font-weight-bold !default;
$close-text-shadow: 0 1px 0 $white !default;

$close: () !default;
$close: map-deep-merge(
	(
		align-items: center,
		appearance: none,
		background-color: transparent,
		border-radius: $btn-border-radius-sm,
		border-width: 0,
		color: $close-color,
		cursor: $link-cursor,
		display: inline-flex,
		font-size: 1rem,
		font-weight: $close-font-weight,
		height: 2rem,
		justify-content: center,
		line-height: 1,
		opacity: 0.5,
		padding: 0,
		text-align: center,
		text-shadow: $close-text-shadow,
		transition: $btn-transition,
		width: 2rem,
		hover: (
			color: $close-color,
			opacity: 0.75,
			text-decoration: none,
		),
		focus: (
			opacity: 0.75,
		),
		disabled: (
			box-shadow: none,
			cursor: $disabled-cursor,
			opacity: 0.25,
			outline: 0,
		),
		disabled-active: (
			pointer-events: none,
		),
		btn-focus-box-shadow: $input-btn-focus-box-shadow,
		btn-focus-outline: 0,
		lexicon-icon-margin-top: 0,
	),
	$close
);

// C Focus Inset

$c-focus-inset-focus: () !default;
$c-focus-inset-focus: map-deep-merge(
	(
		box-shadow: $component-focus-inset-box-shadow,
		border-color: $input-btn-focus-color,
		outline: 0,
	),
	$c-focus-inset-focus
);

// C Inner

$c-inner: () !default;
$c-inner: map-deep-merge(
	(
		align-items: inherit,
		display: inherit,
		flex-direction: inherit,
		height: inherit,
		justify-content: inherit,
		line-height: inherit,
		max-width: inherit,
		min-width: inherit,
		outline: 0,
		overflow: inherit,
		padding-bottom: inherit,
		padding-left: inherit,
		padding-right: inherit,
		padding-top: inherit,
		text-overflow: inherit,
		width: inherit,
	),
	$c-inner
);

// Heading

$heading-spacer-x: 1rem !default; // 16px

$heading-text-margin-bottom: auto !default;
$heading-text-margin-top: auto !default;

// Inline Item

$inline-item-lexicon-icon-margin-top: -0.1em !default;
$inline-item-spacer-x: 0.5rem !default; // 8px

$inline-item-loading-animation-font-size: 1.25em !default;

$page-header-bg: $gray-200 !default;

// Background

$bg-theme-colors: () !default;
$bg-theme-colors: map-deep-merge(
	(
		'primary': (
			background-color: $primary !important,
			hover: (
				background-color: clay-darken($primary, 10%) !important,
			),
		),
		'secondary': (
			background-color: $secondary !important,
			hover: (
				background-color: clay-darken($secondary, 10%) !important,
			),
		),
		'success': (
			background-color: $success !important,
			hover: (
				background-color: clay-darken($success, 10%) !important,
			),
		),
		'info': (
			background-color: $info !important,
			hover: (
				background-color: clay-darken($info, 10%) !important,
			),
		),
		'warning': (
			background-color: $warning !important,
			hover: (
				background-color: clay-darken($warning, 10%) !important,
			),
		),
		'danger': (
			background-color: $danger !important,
			hover: (
				background-color: clay-darken($danger, 10%) !important,
			),
		),
		'light': (
			background-color: $light !important,
			hover: (
				background-color: clay-darken($light, 10%) !important,
			),
		),
		'dark': (
			background-color: $dark !important,
			hover: (
				background-color: clay-darken($dark, 10%) !important,
			),
		),
		'white': (
			background-color: $white !important,
		),
		'transparent': (
			background-color: transparent !important,
		),
	),
	$bg-theme-colors
);

$bg-gradient-theme-colors: () !default;
$bg-gradient-theme-colors: map-deep-merge(
	(
		'primary': (
			background: $primary
				linear-gradient(
					180deg,
					clay-mix($body-bg, $primary, 15%),
					$primary
				)
				repeat-x !important,
		),
		'secondary': (
			background: $secondary
				linear-gradient(
					180deg,
					clay-mix($body-bg, $secondary, 15%),
					$secondary
				)
				repeat-x !important,
		),
		'success': (
			background: $success
				linear-gradient(
					180deg,
					clay-clay-mix($body-bg, $success, 15%),
					$success
				)
				repeat-x !important,
		),
		'info': (
			background: $info
				linear-gradient(180deg, clay-mix($body-bg, $info, 15%), $info)
				repeat-x !important,
		),
		'warning': (
			background: $warning
				linear-gradient(
					180deg,
					clay-mix($body-bg, $warning, 15%),
					$warning
				)
				repeat-x !important,
		),
		'danger': (
			background: $danger
				linear-gradient(
					180deg,
					clay-mix($body-bg, $danger, 15%),
					$danger
				)
				repeat-x !important,
		),
		'light': (
			background: $light
				linear-gradient(180deg, clay-mix($body-bg, $light, 15%), $light)
				repeat-x !important,
		),
		'dark': (
			background: $dark
				linear-gradient(180deg, clay-mix($body-bg, $dark, 15%), $dark)
				repeat-x !important,
		),
	),
	$bg-gradient-theme-colors
);

// Border

$border-theme-colors: () !default;
$border-theme-colors: map-deep-merge(
	(
		'primary': (
			border-color: $primary !important,
		),
		'secondary': (
			border-color: $secondary !important,
		),
		'success': (
			border-color: $success !important,
		),
		'info': (
			border-color: $info !important,
		),
		'warning': (
			border-color: $warning !important,
		),
		'danger': (
			border-color: $danger !important,
		),
		'light': (
			border-color: $light !important,
		),
		'dark': (
			border-color: $dark !important,
		),
		'white': (
			border-color: $white !important,
		),
	),
	$border-theme-colors
);

// Display

$displays: none, inline, inline-block, block, table, table-row, table-cell, flex,
	inline-flex !default;

// Overflow

$overflows: auto, hidden !default;

// Position

$positions: static, relative, absolute, fixed, sticky !default;

// Text

$text-theme-colors: () !default;
$text-theme-colors: map-deep-merge(
	(
		'primary': (
			color: $primary !important,
			hover: (
				color:
					clay-darken(
						$primary,
						$emphasized-link-hover-darken-percentage
					)
					!important,
			),
		),
		'secondary': (
			color: $secondary !important,
			hover: (
				color:
					clay-darken(
						$secondary,
						$emphasized-link-hover-darken-percentage
					)
					!important,
			),
		),
		'success': (
			color: $success !important,
			hover: (
				color:
					clay-darken(
						$success,
						$emphasized-link-hover-darken-percentage
					)
					!important,
			),
		),
		'info': (
			color: $info !important,
			hover: (
				color:
					clay-darken($info, $emphasized-link-hover-darken-percentage)
					!important,
			),
		),
		'warning': (
			color: $warning !important,
			hover: (
				color:
					clay-darken(
						$warning,
						$emphasized-link-hover-darken-percentage
					)
					!important,
			),
		),
		'danger': (
			color: $danger !important,
			hover: (
				color:
					clay-darken(
						$danger,
						$emphasized-link-hover-darken-percentage
					)
					!important,
			),
		),
		'light': (
			color: $light !important,
			hover: (
				color:
					clay-darken(
						$light,
						$emphasized-link-hover-darken-percentage
					)
					!important,
			),
		),
		'dark': (
			color: $dark !important,
			hover: (
				color:
					clay-darken($dark, $emphasized-link-hover-darken-percentage)
					!important,
			),
		),
		'body': (
			color: $body-color !important,
		),
		'muted': (
			color: $text-muted !important,
		),
		'black-50': (
			color: rgba($black, 0.5) !important,
		),
		'white-50': (
			color: rgba($white, 0.5) !important,
		),
	),
	$text-theme-colors
);
